  }

  .postcontent .post-timeline::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 0;
    border-left: 1px dashed #CCC;
    height: 100%;
  }

  .post-timeline .entry-timeline {
    display: none;
    position: absolute;
    border: 2px solid #CCC;
    background-color: #FFF;
    padding-top: 0;
    text-indent: -9999px;
    top: 40px;
    left: auto;
    right: -6px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    font-size: 1.75rem;
    font-weight: bold;
    color: #AAA;
    text-align: center;
    line-height: 1;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
  }

  .post-timeline .entry-timeline .timeline-divider {
    position: absolute;
    top: 4px;
    left: auto;
    right: 15px;
    width: 70px;
    height: 0;
    border-top: 1px dashed #CCC;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
