  .post-timeline .entry.alt:not(.entry-date-section) {
    padding-right: inherit;
    padding-left: 85px;
  }

  .post-timeline .alt .entry-timeline {
    right: auto;
    left: -6px;
  }

  .post-timeline .alt .entry-timeline .timeline-divider {
    left: 15px;
    right: auto;
  }

  .post-timeline .entry-timeline span {
    display: block;
    margin-top: 3px;
    font-size: 13px;
    font-weight: normal;
  }

