  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  max-width: 100%;
  height: 1px;
  background-color: #EEE;
}

.divider::before {
  display: none;
  margin-right: 0.5rem;
}

.divider::after {
