}

.divider a:hover { color: #888; }


/* Divider - Icon Align Right
-----------------------------------------------------------------*/

.divider.divider-right::before {
  display: block;
}

.divider.divider-right::after {
  display: none;
}


/* Divider - Icon Align Center
-----------------------------------------------------------------*/

.divider.divider-center::before {
