/* -----------------------------------------------------------
	SWITCH 2 - ROUND FLAT
-------------------------------------------------------------- */
input.switch-toggle-flat + label {
  padding: 2px;
  width: 60px;
  height: 30px;
  background-color: #DDD;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
